button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.habit {
  font-size: 2.5rem;
  padding: 0.5em;
  list-style: none;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
}

.habit-button:hover {
  opacity: 0.8;
}

.habit-increase,
.habit-decrease {
  color: goldenrod;
}

.habit-delete {
  color: darkred;
}

nav {
  width: 100%;
  height: 100px;
  background-color: wheat;
}

nav > h1 {
  margin-top: auto;
  padding: 26px;
}

.ing {
  width: 40px;
  height: 40px;
  color: white;
  border-radius: 100%;
  background-color: green;
  font-size: 28px;
  display: inline-block;
  text-align: center;
}
